import { themeSwitcherRef } from './common/refs';

const Theme = {
  LIGHT: 'light-theme',
  DARK: 'dark-theme',
};

// –°–∞–º–æ–≤—ã–∑—ã–≤–∞—é—â–∞—è—Å—è —Ñ—É–Ω–∫—Ü–∏—è –Ω–∞ –ø—Ä–æ–≤–µ—Ä–∫—É —Å–æ—Ö—Ä–∞–Ω–µ–Ω–Ω–æ–π —Ç–µ–º—ã
(function checkSavedTheme() {
  const savedTheme = localStorage.getItem('theme');

  if (savedTheme === Theme.DARK) {
    themeSwitcherRef.checked = true;

    switchTheme(Theme.DARK);
  } else {
    switchTheme(Theme.LIGHT);
  }
})();

// –§—É–Ω–∫—Ü–∏—è —Å–º–µ–Ω—ã —Ç–µ–º—ã
function switchTheme(theme) {
  if (theme) {
    document.body.classList.add(theme); // –ï—Å–ª–∏ —Ç–µ–º–∞ —è–≤–Ω–æ —É–∫–∞–∑–∞–Ω–∞, –º–µ–Ω—è–µ—Ç –Ω–∞ –Ω–µ–µ
  } else {
    document.body.classList.toggle(Theme.LIGHT);
    document.body.classList.toggle(Theme.DARK);
  }
}

// –§—É–Ω–∫—Ü–∏—è —Å–æ—Ö—Ä–∞–Ω–µ–Ω–∏—è —Ç–µ–º—ã
function saveTheme() {
  if (themeSwitcherRef.checked) {
    localStorage.setItem('theme', Theme.DARK);
  } else {
    localStorage.setItem('theme', Theme.LIGHT);
  }
}

function onThemeSwitch() {
  switchTheme();
  saveTheme();
}

themeSwitcherRef.addEventListener('click', onThemeSwitch);
